import { LastMessage } from '../types/threadApi';
import { MessageBody } from '../types/message';
import { logger } from '../logger';

/**
 * @hidden
 * 格式化服务端返回的最新一条消息
 */
/**
 *lastMessage数据
{
  	from: "easemob-demo#test-session_wy6@easemob.com/webim_random_1681804469258",
	id: "1134900810367371224",
	payload:{
		"ext":{},
		"bodies":[
			{"msg":"8888",
			"type":"txt"}
		],
		"meta":{},
		"from":"wy6",
		"to":"wy1",
		"type":"chat"
	}
	timestamp: 1681804486240,
	to: "easemob-demo#test-session_wy1@easemob.com/webim_random_1681804408395",
}
*/
function formatMessage(lastMessage: LastMessage) {
	const { id, payload, timestamp, to } = lastMessage;
	const msgBody =
		payload.bodies && payload.bodies.length > 0 ? payload.bodies[0] : {};
	let baseMessage: any = {};
	const extMessage: any = {};
	let chatType = payload.type
		? payload.type
		: to.indexOf('@conference.easemob.com') > -1
		? 'groupChat'
		: 'singleChat';
	chatType = chatType === 'chat' ? 'singleChat' : chatType;
	switch (msgBody.type) {
		case 'txt': {
			const modifiedInfo = payload?.meta?.edit_msg;
			baseMessage = {
				id: id,
				type: 'txt',
				chatType: chatType,
				msg: msgBody.msg || '',
				to: payload.to || '',
				from: payload.from,
				time: timestamp,
				ext: payload.ext,
			};
			// 如果存在修改信息，则为编辑消息
			if (modifiedInfo) {
				const { edit_time, operator, count } = modifiedInfo;
				baseMessage.modifiedInfo = {
					operationTime: edit_time,
					operatorId: operator,
					operationCount: count,
				};
			}
			break;
		}
		case 'img': {
			baseMessage = {
				id: id,
				type: 'img',
				chatType: chatType,
				to: payload.to,
				from: payload.from,
				time: timestamp,
				ext: payload.ext,
				width: msgBody.size?.width || 0,
				height: msgBody.size?.height || 0,
				secret: msgBody.secret || '',
				url: msgBody?.url || '',
				file_length: msgBody.file_length || 0,
				file: {},
			};
			break;
		}
		case 'video': {
			baseMessage = {
				id: id,
				type: 'video',
				chatType: chatType,
				from: payload.from,
				to: payload.to,
				url: msgBody?.url || '',
				secret: msgBody.secret || '',
				filename: msgBody.filename,
				length: msgBody.length || 0,
				file: {},
				file_length: msgBody.file_length || 0,
				filetype: payload.ext.file_type || '',
				ext: payload.ext,
				time: timestamp,
			};
			break;
		}
		case 'loc': {
			baseMessage = {
				id: id,
				type: 'loc',
				chatType: chatType,
				from: payload.from,
				to: payload.to,
				buildingName: msgBody.buildingName || '',
				addr: msgBody.addr,
				lat: msgBody.lat,
				lng: msgBody.lng,
				ext: payload.ext,
				time: timestamp,
			};
			break;
		}
		case 'audio': {
			baseMessage = {
				id: id,
				type: 'audio',
				chatType: chatType,
				from: payload.from,
				to: payload.to,
				secret: msgBody.secret || '',
				ext: payload.ext,
				time: timestamp,
				url: msgBody?.url || '',
				file: {},
				filename: msgBody.filename,
				length: msgBody.length || 0,
				file_length: msgBody.file_length || 0,
				filetype: payload.ext.file_type || '',
			};
			break;
		}
		case 'file': {
			baseMessage = {
				id: id,
				type: 'file',
				chatType: chatType,
				from: payload.from,
				to: payload.to,
				ext: payload.ext,
				time: timestamp,
				url: msgBody?.url || '',
				secret: msgBody.secret || '',
				file: {},
				filename: msgBody.filename || '',
				file_length: msgBody.file_length || 0,
				filetype: payload.ext.file_type || '',
			};
			break;
		}
		case 'cmd': {
			baseMessage = {
				id: id,
				type: 'cmd',
				chatType: chatType,
				from: payload.from,
				to: payload.to,
				ext: payload.ext,
				time: timestamp,
				action: msgBody.action || '',
			};
			break;
		}
		case 'custom': {
			baseMessage = {
				id: id,
				type: 'custom',
				chatType: chatType,
				from: payload.from,
				to: payload.to,
				ext: payload.ext,
				time: timestamp,
				customEvent: msgBody.customEvent || '',
				customExts: msgBody.customExts || '',
			};
			break;
		}
		case 'combine': {
			baseMessage = {
				id: id,
				type: 'combine',
				chatType: chatType,
				msg: msgBody.msg || '',
				to: payload.to || '',
				from: payload.from,
				time: timestamp,
				ext: payload.ext,
				title: msgBody.title || '',
				summary: msgBody.summary || '',
				url: msgBody?.url || '',
				compatibleText: msgBody.text,
				combineLevel: msgBody.combineLevel || 0,
				secret: msgBody.secret || '',
				filename: msgBody.filename || '',
				file_length: msgBody.file_length || 0,
			};
			break;
		}
		default:
			logger.error(`unexpected message: ${lastMessage}`);
			break;
	}
	payload.msgConfig ? (extMessage.msgConfig = payload.msgConfig) : null;
	if (payload?.meta) {
		const meta = payload.meta;
		if (meta.thread) {
			extMessage.chatThread = {
				messageId: meta.thread.msg_parent_id,
				parentId: meta.thread.muc_parent_id,
				chatThreadName: meta.thread.thread_name,
			};
		}
		if (meta.reaction) {
			extMessage.reactions = meta.reaction;
		}
		if (meta.translations) {
			extMessage.translations = meta.translations;
		}
	}
	const formatMessage: MessageBody = {
		...baseMessage,
		...extMessage,
	};
	return formatMessage;
}

export { formatMessage };
